:root {

    /** COLORS **/

    --accent-color: #e83030;
    --secondary-accent-color: #5fccc4;
    --darker-text: #000;
    --dark-text: #333;
    --light-text: #CCC;
    --lighter-text: #FFF;
    --gray-text: #b7b7b7;
    --gray-text-2: #b9b2a2;

    /** BACKGROUNDS **/

    --body-background: #EBE9E4;
    --body-dark-background: #222;
    --menu-background: rgba(242, 242, 242, .98);
    --menu-dark-background: rgba(13, 13, 13, .98);
    --article-background-color: #FFF;
    --article-dark-background-color: #1A1A1A;
    --reading-progress-color: var(--accent-color);
    --lighter-background: #FFF;
    --darker-background: #000;

    /** FONTS **/

    --primary-font: "iAWriterDuospace", "roboto-mono", "Roboto Mono", sans-serif;
    /* --primary-font: "roboto-mono", "Roboto Mono", sans-serif; */
    --secondary-font: "iAWriterQuattro", "Roboto", sans-serif;
    /* --secondary-font: "ff-basic-gothic-pro", "Roboto", sans-serif; */
    --mono-font: "iAWriterMono", monospace;
    /* --mono-font: monospace; */

    /** FONT WEIGHTS **/

    --thin-font: 200;
    --light-font: 300;
    --regular-font: 400;
    --semi-bold-font: 500;
    --bold-font: 600;
    --extra-bold-font: 700;

    /** FONT SIZES **/
    --large-em-font: 1.25rem;
    --x-large-em-font: 1.8rem;
    --xx-large-em-font: 2.6rem;
    --x3-large-em-font: 3rem;
    --x4-large-em-font: 4.5rem;

    /** SECTION SPECIFIC VARIABLES **/

    /** HOME PAGE **/

    --home-title-font-size: var(--x3-large-em-font);
    --home-title-font-weight: var(--regular-font);

    --home-subtitle-font-size: 22px;
    --home-subtitle-font-weight: var(--thin-font);

    /** NAV LINKS **/

    --nav-link-color: #262626;
    --nav-link-font-size: 15px;
    --nav-link-font-family: var(--primary-font);
    --nav-link-letter-spacing: 5px;
    --nav-link-font-weight: var(--regular-font);
    --nav-link-text-transform: uppercase;
    --nav-link-hover-color: var(--accent-color);

    /** POST AUTHOR **/

    --author-name-font-family: var(--primary-font);
    --author-name-font-size: 12px;
    --author-name-font-weight: var(--regular-font);
    --author-name-letter-spacing: 3px;
    --author-name-color: var(--accent-color);
    --author-name-transform: uppercase;

    /** POSTS **/

    --post-title-font-family: var(--primary-font);
    --post-title-font-size: 70px;
    --post-title-font-weight: var(--light-font);
    --post-title-color: var(--dark-text);
    --post-title-hi-color: var(--lighter-text);

    --post-date-font-family: var(--primary-font);
    --post-date-color: var(--lighter-text);
    --post-date-font-size: 15px;

    --post-author-font-family: var(--primary-font);
    --post-author-color: var(--gray-text-2);
    --post-author-hi-color: var(--lighter-text);
    --post-author-color-hover: var(--accent-color);
    --post-author-font-size: 15px;
    --post-author-transform: uppercase;

    --post-page-num-font-family: var(--primary-font);
    --post-page-num-font-size: 15px;
    --post-page-num-color: var(--accent-color);
    --post-page-num-hi-color: var(--lighter-text);

    /** RELATED POSTS **/

    --related-date-font-family: var(--primary-font);
    --related-date-font-size: 14px;
    --related-date-color: var(--gray-text);
    --related-date-transform: capitalize;

    --related-title-font-family: var(--secondary-font);
    --related-title-color: var(--accent-color);
    --related-title-color-hover: var(--dark-text);
    --related-title-font-size: var(--large-em-font);
    --related-title-font-weight: var(--bold-font);
    --related-title-transform: capitalize;

    --related-author-color: var(--gray-text);
    --related-author-font-size: 14px;
    --related-author-font-weight: var(--regular-font);
    --related-author-link-font-weight: var(--semi-bold-font);

    /** FOOTER **/

    --footer-title-color: var(--accent-color);
    --footer-title-font-size: 20px;
    --footer-title-transform: uppercase;
    --footer-title-letter-spacing: 2px;

    --footer-tags-font-weight: var(--regular-font);

    --footer-copyright-font-family: var(--primary-font);
    --footer-copyright-font-weight: var(--regular-font);
    --footer-copyright-font-size: 14px;

}

::selection {
    background: rgba(0, 0, 0, 0.2);
}
::-moz-selection {
    background: rgba(0, 0, 0, 0.2);
}

body{
    font-family: var(--secondary-font);
    background: var(--body-background);
    color: var(--dark-text);
    font-weight: var(--regular-font);
}

a{
    color: var(--accent-color);
    text-decoration: none;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

a:hover{
    color: var(--dark-text);
    text-decoration: none;
}

h1{
    font-size: 38px;
}

h2{
    font-size: 32px;
}

h3{
    font-size: 26px;
}

h4{
    font-size: 22px;
}

h5{
    font-size: 18px;
}

h6{
    font-size: 14px;
}

h1, h2, h3, h4, h5, h6{
    font-family: var(--primary-font);
    color: var(--accent-color);
    margin: 30px 0;
    font-weight: var(--light-font);
    margin-top: 90px;
    line-height: 1.4;
}

p{
    font-size: 22px;
    line-height: 29px;
    font-weight: var(--thin-font);
    margin-bottom: 30px;
}

ul, ol{
    padding-left: 25px;
}

li{
    font-size: 22px;
    line-height: 29px;
    font-weight: var(--thin-font);
}

pre{
    font-family: var(--mono-font);
    background-color: #f1f1f1;
    display: block;
    border: 1px solid #cccccc;
    padding: 22px 25px;
    font-size: 14px;
    line-height: 25px;
    white-space: pre;
    color: var(--dark-text);
    margin-bottom: 30px;
}

pre code{
    border: none;
}

code{
    font-family: var(--mono-font);
    background-color: #f1f1f1;
    display: inline-block;
    border: 1px solid #f1f1f1;
    padding: 0px 7px;
    margin: 1px 5px;
    font-size: 14px;
    line-height: 25px;
    white-space: pre;
    vertical-align: bottom;
    color: var(--dark-text);
    border-radius: 5px;
}

mark {
    background: #ec5858;
    color: #fff;
}

blockquote{
    margin: 60px 0 60px -103px;
    font-size: 32px;
    letter-spacing: -1px;
    line-height: 42px;
    color: var(--accent-color);
    position: relative;
}

blockquote:before {
    content: '';
    width: 50px;
    height: 40px;
    background-image: url(../img/quotes.svg);
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    top: -20px;
    left: 0;
    margin-left: -25px;
    background-size: contain;
}

blockquote p{
    font-size: 32px;
    letter-spacing: -1px;
    line-height: 42px;
    color: var(--accent-color);
}

input{
    height: 64px;
    border-radius: 3px;
    background-color: #FFFFFF;
    box-shadow: 0 0 8px 0 rgba(200,195,183,0.3);
    border: none;
    width: 100%;
    padding: 0 20px;
}

figure{
    margin: 0;
}

.btn{
    border-radius: 3px;
    background: var(--accent-color);
    color: #fff;
    border-color: var(--accent-color);
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    text-transform: uppercase;
    font-family: var(--primary-font);
    font-size: 16px;
}

.btn:hover{
    border-color: var(--accent-color);
    color: var(--accent-color);
    background: var(--lighter-background);
    box-shadow: 0 0 8px 0 rgba(200,195,183,0.3);
}

.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show > .btn-primary.dropdown-toggle:focus {
    background-color: #FFF;
    box-shadow: 0 0 8px 0 rgba(200,195,183,0.3);
    border-color: var(--accent-color);
    color: var(--accent-color);
}

.btn-outline {
    background-color: transparent;
}

.btn-outline.btn-primary,
.btn-outline.btn-primary:hover,
.btn-outline.btn-primary:active,
.btn-outline.btn-primary:focus {
    border-color: var(--accent-color);
    color: var(--accent-color);
}

/* ==========================================================================
   2. Header
   ========================================================================== */

header,
.modal-header{
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: center;
    border: none;
    padding: 30px 0;
}

header .blog-logo{
    text-align: center;
}

header .blog-logo a{
    color: var(--dark-text);
    font-size: 20px;
    font-weight: var(--light-font);
    line-height: 26px;
    display: inline-block;
}

header .blog-logo a span{
    padding: 30px 0;
    display: inline-block;
}

header .blog-logo a:hover{
    color: var(--accent-color);
}

header .blog-logo a img{
    max-width: 250px;
    max-height: 150px;
    padding: 30px 0;
}

header .modal .blog-logo{
    color: var(--gray-text);
}

header .btns {
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 0;
}

header .btns a {
    color: var(--gray-text-2);
    margin: 0 20px;
    position: relative;
}

header .btns a .counter {
    position: absolute;
    top: -9px;
    left: -9px;
    font-size: 10px;
    /*background: var(--accent-color);*/
    font-family: var(--primary-font);
    color: #fff;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    transform: scale(1);
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

header .btns a .counter:before{
    content: '';
    border-radius: 50%;
    width: 20px;
    height: 20px;
    left: 50%;
    margin-left: -10px;
    background: var(--accent-color);
    position: absolute;
    z-index: -1;
}

header .btns a .counter.shake:before{
    -webkit-animation: scale-shake 1s linear;
    animation: scale-shake 1s linear
}

header .btns a .counter.hidden{
    transform: scale(0);
}

header .btns a svg{
    display: block;
}

header .btns a svg,
header .btns a svg path{
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

header .dark-icon,
.dark header .light-icon {
    display: none;
}

.dark header .dark-icon {
    display: block;
}

header .btns a svg path {
    fill: var(--gray-text-2);
}

header .btns a:hover svg path {
    fill: var(--accent-color);
}

header #color-switcher svg {
    stroke: var(--gray-text-2);
}

header #color-switcher:hover svg {
    stroke: var(--accent-color);
}

header .btns a:last-child{
    margin-right: 0;
}

#homepage nav ul,
header nav ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
    text-align: center;
    width: 100%;
}

#homepage nav ul li,
header nav ul li{
    border-bottom: 1px solid rgba(185, 178, 162, 0.15);
}

#homepage nav ul li:last-child,
header nav ul li:last-child{
    border: none;
}


#homepage nav ul li a,
header nav ul li a{
    color: var(--nav-link-color);
    font-size: var(--nav-link-font-size);
    font-family: var(--nav-link-font-family);
    letter-spacing: var(--nav-link-letter-spacing);
    line-height: 20px;
    font-weight: var(--nav-link-font-weight);
    text-transform: var(--nav-link-text-transform);
    padding: 25px;
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
}

#homepage nav ul li a:hover,
header nav ul li a:hover{
    color: var(--nav-link-hover-color);
}

.dark header .blog-logo a img {
    filter: invert(1);
}

header .modal-footer{
    border: none;
    justify-content: center;
    min-height: 150px;
}

header #search-field{
    border: 1px solid #B9B2A2;
    border-radius: 0;
    box-shadow: none;
    background: transparent;
    color: #262626;
    font-size: 15px;
    line-height: 20px;
    font-family: var(--primary-font);
}

header #search-field:focus{
    outline: 0;
}

header h5{
    margin-top: 60px;
    color: var(--gray-text);
    font-size: 15px;
    line-height: 20px;
    text-transform: uppercase;
    font-weight: var(--regular-font);
    margin-bottom: 10px;
}

header h5:before{
    content: '#';
}

.modal-open .modal{
    overflow-y: scroll;
}

.modal-open #menu {
    overflow-y: auto;
}

.modal .col-md-12{
    padding: 0;
}

.modal .modal-body{
    padding: 0;
}

#menu .modal-body{
    display: flex;
    align-items: center;
}

.modal-backdrop.show{
    opacity: 0;
}

.fade-scale {
    opacity: 0;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.fade-scale.show {
    opacity: 1;
}

.modal{
    padding-right: 0 !important;
}

.modal:not(#contactModal) h3{
    font-size: 15px;
    letter-spacing: 5px;
    color: #262626;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    padding: 25px;
    border-bottom: 1px solid rgba(185, 178, 162, 0.15);
    margin: 0;
    font-weight: var(--regular-font);
}

.modal .modal-content{
    border: none;
    background-color: var(--menu-background);
}

.modal .modal-dialog{
    max-width: 100%;
    margin: 0;
    display: flex;
    min-height: 100vh;
    width: 100%;
}

.modal .modal-dialog .modal-content{
    width: 100%;
    border-radius: 0;
    min-height: 100vh;
}

.list-box{
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.list-box li{
    border-radius: 10px;
    background-color: #FFFFFF;
    box-shadow: 0 0 8px 0 rgba(200,195,183,0.3);
    padding: 15px 20px 15px 58px;
    width: calc(50% - 5px);
    margin-bottom: 10px;
    position: relative;
}

.list-box li time{
    color: #51C3B7;
    font-size: 15px;
    line-height: 27px;
    font-family: var(--primary-font);
    display: block;
    font-weight: var(--regular-font);
}

.list-box li a{
    color: #262626;
    font-size: 19px;
    letter-spacing: -1px;
    line-height: 25px;
    font-family: var(--primary-font);
    font-weight: var(--regular-font);
}

.list-box li a:hover{
    color: var(--accent-color);
}

.bookmark-container .no-bookmarks{
    font-size: 17px;
    text-align: center;
    margin: 100px;
}

.bookmark-container .no-bookmarks .circle{
    border: 1px solid var(--secondary-accent-color);
    width: 18px;
    height: 18px;
    border-radius: 50%;
    display: inline-block;
    margin: 0 2px -2px 2px;
}

.social{
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
}

.social li{
    display: inline-block;
}

.social li a{
    color: var(--gray-text);
    margin: 0 5px;
    padding: 5px;
    display: flex;
    align-items: center;
}

.social svg {
    width: 19px;
    height: 19px;
    fill: var(--gray-text);
    stroke: var(--gray-text);
}

