/* ==========================================================================
   9. Account Dropdown
   ========================================================================== */

.account-menu {
    position: relative;
}

.account-menu-avatar {
    position: relative;
    display: block;
    margin: 0;
    border-radius: 100%;
    cursor: pointer;
}

.account-menu-avatar img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 100%;
}

.account-menu ul {
    visibility: hidden;
    position: absolute;
    top: 50px;
    right: 8px;
    z-index: 9998;
    padding: 15px 0;
    min-width: 250px;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 0 8px 0 rgba(200,195,183,.3);
    list-style: none;
    opacity: 0;
    transition: all 0.3s cubic-bezier(0.4,0.01,0.165,0.99), opacity 0.2s cubic-bezier(0.4,0.01,0.165,0.99);
    transform: translateY(-6px) scale(0.95);
    transform-origin: top right;
    pointer-events: none;
}

.account-menu-avatar.active + ul {
    visibility: visible;
    opacity: 1.0;
    transform: translateY(0) scale(1);
    pointer-events: all;
}

.account-menu ul:before {
    content: "";
    position: absolute;
    top: -8px;
    right: 14px;
    z-index: 9998;
    width: 0;
    height: 0;
    border-width: 0 10px 10px;
    border-style: solid;
    border-color: transparent transparent #fff;
}

.account-menu ul li {
    margin: 0;
    padding: 0;
}

.account-menu .account-info {
