/* ==========================================================================
   13. Dark Mode
   ========================================================================== */

body.dark {
    background-color: var(--body-dark-background);
    color: var(--light-text);
}

.dark .article-container,
.dark .post-content::before,
.dark .list-box li {
    background-color: var(--article-dark-background-color);
}

.dark .post-upgrade-cta-content,
.dark article .kg-bookmark-container,
.dark .article-container {
    box-shadow: 0 0 41px 0 rgba(0, 0, 0, 0.15);
}

.dark .article-container .post-content figcaption,
.dark .article-container .post-content .kg-gallery-card figcaption,
.dark .article-container .post-content .kg-image-card figcaption,
.dark .author.has-image,
.dark .loop .swiper-slide.loaded:not(.swiper-slide-active) .read-later,
.dark .loop .swiper-slide.loaded:not(.swiper-slide-active) .article-container,
.dark .account-menu .account-info,
.dark .modal h3,
.dark header #search-field,
.dark header nav ul li,
.dark #homepage nav ul li,
.dark code,
.dark #contactModal .form-control {
    /* border-color: hsla(0,0%,100%,.05); */
}

.dark .loop .swiper-slide.loaded:not(.swiper-slide-active) .read-later::before {
    background: hsla(0,0%,100%,.05);
}

.dark .account-menu ul a,
.dark .article-container .post-title,
.dark header .blog-logo a,
.dark .author p,
.dark header #search-field,
.dark header nav ul li a,
.dark #homepage nav ul li a,
.dark code {
    color: var(--light-text);
}

.dark .account-menu ul a:hover,
.dark a:hover,
.dark .list-box li a,
.dark .modal h3,
.dark .author .author-meta a:hover {
    color: var(--lighter-text);
}

.dark .btn:hover,
.dark .btn:active,
.dark .btn-primary:not(:disabled):not(.disabled).active,
.dark .btn-primary:not(:disabled):not(.disabled).active:focus,
.dark .btn-primary:not(:disabled):not(.disabled):active,
.dark .btn-primary:not(:disabled):not(.disabled):active:focus,
.dark .show > .btn-primary.dropdown-toggle,
.dark .show > .btn-primary.dropdown-toggle:focus,
.dark .btn:focus,
.dark .account-menu ul,
.dark .checkout-plan,
.dark #contactModal .form-control {
    background: var(--darker-background);
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
}

.dark #contactModal .form-control {
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15) !important;
}

.dark .signup-box input,
.dark .signin-box input,
.dark code {
    background: var(--darker-background);
}

.dark .account-menu ul,
.dark .signin-box,
.dark .signup-box,
.dark .list-box li {
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
}

.dark .account-menu ul::before {
    border-color: transparent transparent var(--darker-background);
}

.dark .loop .swiper-slide.loaded:not(.swiper-slide-active) .author-image a,
.dark .loop .swiper-slide:not(.swiper-slide-active):not(.first) .article-container .post-content .img-holder span,
.dark .loop .swiper-slide:not(.swiper-slide-active):not(.first) .related-post .img-holder {
    box-shadow: none;
    background: transparent;
}

.dark blockquote::before {
    filter: invert(0.75);
}

.dark .modal .modal-content {
    background: var(--menu-dark-background);
}

.dark .list-box li a:hover,
.dark header .blog-logo a:hover,
.dark header nav ul li a:hover,
.dark .share-on a:hover,
.dark #contactModal h3,
.dark #homepage nav ul li a:hover {
    color: var(--accent-color);
}

.dark #contactModal .modal-content {
    background-color: var(--article-dark-background-color);
}
